<template>
  <div id="app">
    <img alt="Vue logo" src="./assets/logo.png">
    <h1>vue-awesome-countdown demo</h1>
    <countdown :end-time="new Date().getTime() + 60000">
      <template
        slot="process"
        slot-scope="vm">{{ `Lefttime: ${vm.timeObj.ceil.s}` }}</template>
      <template slot="finish">Done!</template>
    </countdown>
  </div>
</template>

